.search {
  width: 100%;
  margin-top: 15vh;
  margin-bottom: 20px;
  padding: 0 18px;
  height: 40px;
  border-radius: 40px;
  outline: none;
  color: $mainColor;
  font-size: 18px;
  border: 2px solid $darkColor;
  background: $transBlack;
  transition: all 0.1s linear;

  @include desktop {
    height: 60px;
      padding: 24px;
      font-size: 24px;
  }

  &::placeholder {
      font-style: italic;
      color: $medColor;
  }

  &:hover {
      border: 2px solid $medColor;
  }

  &:focus {
      border: 2px solid $mainColor;
  }
}